.answer_sheet_title {
  margin-top: 2mm;
  margin-bottom: 10mm;
  font-size: 7mm;
  font-weight: bold;
  text-align: center;
}

.answer_sheet_header {
  font-size: 3mm;
}
.answer_sheet_header > table, .answer_sheet_header > table > tr, .answer_sheet_header > table > tr > td {
  border-collapse: collapse;
  border: 1px solid black;
  text-align: left;
}
.answer_sheet_header > table table {
  border-collapse: collapse;
  border: 1px solid black;
  text-align: center;
  margin: 2mm;
}
.answer_sheet_header > table tr {
  border-collapse: collapse;
  border: 1px solid black;
  text-align: center;
  min-height: 5mm;
}
.answer_sheet_header > table td {
  border-collapse: collapse;
  border: 1px solid black;
  text-align: center;
}
.use_answer_sheet_font {
  font-family: answersheet;
  font-size: 5mm;
}

.answer_sheet_group_title {
  text-align: left;
  font-size: 4mm;
  margin-top: 7mm;
  margin-bottom: 1mm;
}

.answer_sheet_problem_title {
  text-align: left;
  font-size: 3mm;
  margin-bottom: 1mm;
  margin-top: 1mm;
}

.answer_sheet_answer_area_subjective_subarea {
  list-style-type: none;
  margin-bottom: 1mm;
  margin-top: 1mm;
  padding-left: 2mm;
}
.answer_sheet_answer_area_subjective {
  width: 100%;
}
.answer_sheet_answer_area_subjective > table{
  width: 100%;
  border-collapse: collapse;
  border: 1px solid black;
  text-align: left;
  table-layout: fixed;
}
.answer_sheet_answer_area_subjective > table > tr {
  border-collapse: collapse;
  border: 1px solid black;
}
.answer_sheet_answer_area_subjective > table > tr > td {
  border-collapse: collapse;
  border: 1px solid black;
  vertical-align: top;
}
.answer_sheet_answer_area_subtitle {
  font-size: 3mm;
}

.answer_sheet_answer_area_objective {
  width: 100%;
  height: 50mm;
  border: 1px solid black;
  position: relative;
}
.answer_sheet_answer_area_objective:hover .resizer {
  display: block;
}
.resizer {
  width: 17px;
  height: 17px;
  position:absolute;
  right: 0;
  bottom: -9px;
  cursor: move;
  background-image: url(data:image/png;base64,);
  background-size: 100% 100%;
  display: none;
}

.column_spanner {
  position: absolute;
  height: 4mm;
  width: 4mm;
  background-color: black;
}
